// === VARIABLES ===
$breakpoint-ex-sm-max: 360px;
$breakpoint-sm-max: 648px;
$breakpoint-md-max: 768px;
$breakpoint-lg-max: 992px;
$breakpoint-xl-max: 1200px;
$breakpoint-xxl-max: 1400px;

// === / VARIABLES ===

// === COMMON ===

body {
  font-family: "Source Sans Pro", "Arial", sans-serif;
  color: #3f3f3f;
}

.container {
  max-width: 1376px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;

  @media (max-width: $breakpoint-sm-max) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.btn {
  font-family: "Inter", "Arial", sans-serif;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  background: none;
  border-radius: 10px;
  transition: 0.2s;
  z-index: 1;
  border: none;

  &__submit {
    background: #eeeeee;
    width: 100%;
    margin-top: 14px;
    padding-top: 8px;
    padding-bottom: 13px;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #b4b4b4;
    letter-spacing: -0.02em;
  }
}

// --------------header-------------- //
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 14px;

  &__heading {
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 35px;
  }
}

.select {
  position: relative;
  width: 120px;
  height: 36px;
  background: #fffefb;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.select.is-active > .select__body {
  display: block;
}

.select__header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
  justify-content: center;
}

.select__icon {
  align-items: center;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  text-align: center;
  margin-left: 5px;
}

.select__current {
  color: #b4b4b4;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
}

.select__body {
  background-color: #fff;
  box-sizing: border-box;
  border-top: 0;
  display: none;
  left: 0;
  position: absolute;
  right: 0;
  top: 102%;
  z-index: 5;
}
.select__item {
  cursor: pointer;
  color: #b4b4b4;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  padding-top: 8px;
  padding-right: 8px;
  padding-bottom: 8px;
  padding-left: 20px;

  @media (max-width: $breakpoint-sm-max) {
    padding-left: 9px;
  }
}

.select__item:hover {
}

.select__img {
  vertical-align: bottom;
  margin-left: 7px;
}
// --------------/header-------------- //

// --------------main-------------- //
.main {
  display: grid;
  grid-template-columns: 335px 1fr;
  gap: 14px;

  @media (max-width: $breakpoint-md-max) {
    grid-template-columns: 1fr;
  }
}

.ad-form {
  background: #fffefb;
  box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.04),
    0px 6px 10px rgba(0, 0, 0, 0.02);
  border-radius: 4px;
  padding-left: 10px;
  padding-right: 13px;
  padding-top: 11px;
  padding-bottom: 18px;
  position: sticky;
  top: 0;

  &__element {
    border: none;
    padding-bottom: 6px;
  }

  &__input {
    width: 100%;
    display: block;
    border: none;
    background: #fffefb;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #b4b4b4;
    box-sizing: border-box;
    padding-left: 16px;
    padding-top: 10px;
    padding-bottom: 11px;
    padding-right: 10px;

    &--description {
      height: 108px;
      resize: none;
    }
  }

  &__label {
    position: relative;
    font-weight: normal;
    font-size: 10px;
    line-height: 13px;
    color: #49485e;
    letter-spacing: -0.02em;
    margin-bottom: 5px;
    display: inline-block;

    &::before {
      position: absolute;
      content: "";
      width: 4px;
      height: 4px;
      right: -5px;
      top: 0;
      border-radius: 4px;
      background-color: #ff8484;
    }

    &--description {
      &::before {
        display: none;
      }
    }
  }
}

.products {
  display: grid;
  grid-template-columns: repeat(auto-fill, 332px);
  gap: 15px;

  @media (max-width: $breakpoint-sm-max) {
    justify-content: center;
  }
}

.product {
  width: 332px;
  cursor: pointer;
  position: relative;

  &:hover .delete {
    display: flex;
  }

  &__img-wrap {
    height: 200px;
    border-radius: 4px 4px 0px 0px;
  }

  &__img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 4px 4px 0px 0px;
  }

  &__heading {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 25px;
    margin-left: 15px;
    margin-top: 14px;
  }

  &__description {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    margin-left: 15px;
    margin-right: 15px;
    margin-bottom: 31px;
  }

  &__price {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    margin-left: 15px;
    margin-right: 15px;
  }
}

.delete {
  display: none;
  cursor: pointer;
  position: absolute;
  width: 32px;
  height: 32px;
  right: -8px;
  top: -11px;
  background-color: #ff8484;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: #3f3f3f;
  }
}
// --------------/main-------------- //

// === / COMMON ===
